.shop-report-page-container {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;

  .shopname {
    text-align: center;
    margin-bottom: 0;
  }

  .business-analysis-header-fixed {
    width: 100%;

    .business-analysis-time {
      display: flex;
      justify-content: space-between;
      align-items: center;
      border-bottom: 1px solid #eee;
      padding: 15px 0px;

      .prev-btn,
      .next-btn {
        padding: 20px;
      }

      .time-content {
        display: flex;
        flex-direction: column;
        justify-content: center;

        .date-content {
          font-size: 16px;
          color: #1c1c1c;
          text-align: center;
        }
      }

      .toggle-content {
        display: flex;
        justify-content: center;
        margin-top: 15px;

        .mode+.mode {
          margin-left: 10px;
        }

        .mode {
          border: 1px solid #ccc;
          border-radius: 20px;
          padding: 4px 10px;
          font-size: 12px;

          .text {
            display: inline-block;
            margin-right: 4px;
          }

          .ladyn {
            font-size: 12px;
            color: #999;
          }
        }
      }
    }

    .business-analysis-filter {
      display: flex;
      padding: 5px;

      .filter-item {
        background-color: rgba(244, 245, 249, 0.8);
        border-radius: 5px;
        padding: 5px 10px;
        margin: 5px;
        font-size: 14px;
        box-shadow: 0 2px 2px 1px #f5f5f5;

        &.active {
          background-color: rgba(56, 130, 173, 0.53);
          color: #fff;
        }
      }
    }
  }

  .list {
    .content-wrap {
      padding: 10px;

      .title {
        font-size: 14px;
        margin: 0 0 10px 0;
      }

      .content-1 {
        display: flex;
        flex-wrap: wrap;

        .item {
          flex: 0 0 50%;
          padding: 0 30px 0 10px;
          margin-bottom: 10px;
          box-sizing: border-box;

          .sub-item {
            background-color: #fff;
            padding: 10px;

            .subtitle {
              font-size: 10px;
              margin-bottom: 4px;
            }

            .text {
              font-size: 20px;
              color: #38aaf1;
            }
          }
        }
      }

      .content-2 {
        .item {
          background-color: #fff;
          margin-bottom: 10px;
          padding: 10px;

          &.two {
            .content {
              display: flex;

              .sub-item {
                flex: 0 0 50%;
              }
            }
          }

          &.one {
            .content {
              .sub-item {
                .sub-item-content {
                  display: flex;
                  align-items: center;
                  justify-content: space-between;
                }
              }
            }
          }

          .content {
            .sub-item {
              padding: 0 30px 0 10px;
              margin-bottom: 10px;
              box-sizing: border-box;

              .sub-item-content {
                padding: 10px;
                background-color: rgba(130, 130, 130, 0.1);

                .sub-item-title {
                  font-size: 10px;
                  margin-bottom: 4px;
                }

                .sub-item-text {
                  font-size: 20px;
                  color: #38aaf1;
                }
              }
            }
          }

          .subtitle {
            display: flex;
            justify-content: space-between;
            margin-bottom: 10px;

            .text {
              font-size: 12px;
            }

            .unit {
              font-size: 10px;
            }
          }
        }
      }
    }
  }
}