.group-report-page-container {
  .business-analysis-header-fixed {

    .business-analysis-time {
      display: flex;
      justify-content: space-between;
      align-items: center;
      border-bottom: 1px solid #eee;
      padding: 15px 0px;

      .prev-btn,
      .next-btn {
        padding: 20px;
      }

      .time-content {
        display: flex;
        flex-direction: column;
        justify-content: center;

        .date-content {
          font-size: 16px;
          color: #1c1c1c;
          text-align: center;
        }
      }

      .toggle-content {
        display: flex;
        justify-content: center;
        margin-top: 15px;

        .mode {
          border: 1px solid #ccc;
          border-radius: 20px;
          padding: 4px 10px;
          font-size: 12px;

          .text {
            display: inline-block;
            margin-right: 4px;
          }

          .ladyn {
            font-size: 12px;
            color: #999;
          }
        }
      }
    }

    .business-analysis-filter {
      display: flex;
      padding: 5px;

      .filter-item {
        background-color: rgba(244, 245, 249, 0.8);
        border-radius: 5px;
        padding: 5px 10px;
        margin: 5px;
        font-size: 14px;
        box-shadow: 0 2px 2px 1px #f5f5f5;

        &.active {
          background-color: rgba(56, 130, 173, 0.53);
          color: #fff;
        }
      }
    }

    // .business-analysis-indicator {
    //   display: flex;
    //   align-items: center;
    //   border-bottom: 1px solid #e9e9e9;

    //   .left-fixed {
    //     flex: 0 0 100px;
    //     padding: 10px;
    //   }

    //   .sub-content {
    //     width: calc(100% - 100px);
    //     display: flex;
    //     flex-wrap: nowrap;
    //     word-break: keep-all;
    //     overflow-x: auto;

    //     .text {
    //       flex: 1;
    //       display: inline-block;
    //       padding: 10px
    //     }
    //   }
    // }
  }

  .business-analysis-content {
    height: calc(100% - 132px);
    overflow-y: auto;

    .shop-item-indicator {
      display: flex;
      position: relative;
      font-weight: 300;

      &::after {
        content: '';
        width: 100%;
        height: 1px;
        background-color: #eee;
        position: absolute;
        top: 35px;
        left: 0;
      }

      .indicator-title {
        position: relative;
        padding-bottom: 10px;
      }

      .left-fixed {
        flex: 0 0 120px;

        .item+.item {
          margin-top: 20px;
        }

        .item {
          display: flex;
          flex-direction: column;

          .text {
            &:nth-child(even) {
              background-color: rgba(247, 246, 251, 0.8);
            }

            display: inline-block;
            padding: 5px 10px;

            &.title {
              font-weight: bold;
              white-space: nowrap;
            }
          }
        }
      }

      .sub-content {
        width: calc(100% - 120px);
        flex-wrap: nowrap;
        word-break: keep-all;
        overflow-x: auto;

        .sub-item+.sub-item {
          margin-top: 20px;
        }

        .sub-item {
          .line-item {
            display: flex;

            &:nth-child(even) {
              .item {
                background-color: rgba(247, 246, 251, 0.8);
              }
            }

            .item-title {
              flex: 0 0 60px;
              text-align: center;
              padding: 5px 10px;
            }

            .item {
              flex: 0 0 60px;
              text-align: center;
              padding: 5px 10px;
              font-weight: bold;

              &.increase {
                color: #e84124;
              }

              &.reduction {
                color: #12da29;
              }
            }
          }
        }
      }
    }
  }
}