.check-page-container {
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;

  .scan-content {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    .scan-img {
      width: 140px;
    }

    .scan-text {
      color: #bfbfbf;
      font-size: 16px;
      font-weight: bold;
    }
  }
}